body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

#root {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App {
  text-align: center;
  height: 100;
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

.App-intro {
  font-size: large;
}

.Main-Container {
  padding: 1.5rem;
  background: #00e9de;
}

.Current-Playlist {
  text-align: center;
}

.Search-Bar-Wrapper {
  flex: 1 0 auto;
  background: #222;
  padding-top: 1.5rem;
}

@media only screen and (min-width: 1000px) {
  .Player-View-Container {
    display: inline-flex;
  }
  .Current-Playlist {
    padding-right: 2rem;
    text-align: left;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 15s linear;
            animation: App-logo-spin infinite 15s linear;
    height: 4.5rem;
}

.App-header {
    background-color: #222;
    height: 15rem;
    padding: 1.5rem;
    padding-top: 2rem;
    color: white;
    font-family: 'Roboto', sans-serif;
}

.App-title {
    margin-top: 1.5rem;
    font-size: 1.5rem;
}

.Now-Playing-Icon {
    margin-right: 1.5rem;
}

.Username-Field {
    color: #00e9de
}
.Suggestion-Container {
    list-style: none;
    display: inline;
}

.Suggestion-Thumbnail {
    height: 45%;
    width: 65%;
}

.Video-List {
    padding: 0;
}

.Panel-Wrapper {
    border-color: #00e9de;
}

.Panel-Wrapper > .panel-body {
    background: #3a3a3a;
}

.Panel-Wrapper > .panel-heading {
    background: #1d1d1d;
    color: white;
    font-weight: bold;
}

.Add-Video-Button {
    margin-top: 1rem;
}
.Search-Bar-Container {
    margin: auto;
    max-width: 120rem;
}

.Search-Bar {
    height: 1.5rem;
    padding: 1.5rem;
    border-radius: 0.3rem;
}

.Video-Suggestions {
    padding: 1rem;
    background: #222;
}

.Search-Video-Prompt-Container {
    color: white;
    font-family: 'Roboto', sans-serif;
}

.Search-Video-Prompt-Suffix {
    color: #00e9de;
    font-family: 'Roboto', sans-serif;
}

.Head-Phone-Icon {
    margin-top: 2rem;
    font-size: 8.5rem;
}
.Youtube-Player-Wrapper {
    margin-bottom: 1.5rem;
    margin-left: 1.5rem;
    padding: 1.5rem;
    background: rgb(0, 0, 0, 0.7);
}

.Youtube-Player {
    pointer-events: none;
    padding-bottom: 0.5rem;
}

.Youtube-Player-Debug {
    padding-bottom: 0.5rem;
}

.Video-Banner-Title {
    font-size: 1.5rem;
    color: white;
    margin-top: 0;
    margin-bottom: 0;
    font-family: 'Roboto', sans-serif;
}

.Suggested-User {
    color: #00e9de;
}

.Skip-Video-Button {
    margin-left: 2rem;
}

.Skip-Icon {
    margin-left: 0.2rem;
}
.Playlist-Section {
    padding: 1rem;
    background: #222222;
    font-family: 'Roboto', sans-serif;
}

.Playlist-Header {
    color: white;
    text-align: center;
    margin-top: 0.5rem;
}

.Video-Playlist {
    list-style: none;
    padding: 0;
    padding-bottom: 0;
    height: 25rem;
    overflow: auto;
    width: 45rem;
}

/* Prettier scrollbar, courtesy of: https://codepen.io/devstreak/pen/dMYgeO */
.Video-Playlist::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.Video-Playlist::-webkit-scrollbar {
	width: 6px;
	background-color: #F5F5F5;
}

.Video-Playlist::-webkit-scrollbar-thumb {
	background-color: #000000;
}

.Music-Icon {
    margin-right: 0.8rem; 
}

.Playlist-Item-Wrapper:first-child {
    color: #00e9de;
    font-weight: 500;
}

.Playlist-Item-Wrapper {
    color: white;
}

.Playlist-Item {
    padding: 0.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.Playlist-Item:hover {
    background: rgb(0, 0, 0, 0.7);
}

.Seperator {
    margin: 0;
    background: #a59c9c;
}

.Empty-Video-Background {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    color: white;
    width: inherit;
    height: inherit;
}

.No-Video-Text {
    margin: 0;
    font-size: 1.6rem;
}

.Pause-Icon {
    font-size: 5rem;
    padding-bottom: 1.5rem;
}
/* Overide bootstrap navbar */

.navbar {
    border-radius: 0;
    border-bottom: 1px solid #a59c9c;
    margin-bottom: 0; 
}

.navbar > .container {
    width: 85%;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

.navbar-inverse .navbar-brand {
    font-weight: 550;
    color: #00e9de;
}

#Users-Nav-Dropdown {
    color: white;
}

.User-Icon { 
    padding: 0.4rem;
    border: 1.5px solid;
    border-radius: 1.5rem;
}

.Username-List-Item {
    padding-left: 1rem;
}
